import { connect } from 'react-redux';
import StationView from '../components/stationView';
import { getStationData } from '../actions/stationActions';

export default connect(
  (state: any, props) => ({}),
  (dispatch: Function, props) => ({
    getStationData: (stationName: string) => dispatch(getStationData(stationName)),
  })
)(StationView);
