import { Typography } from '@material-ui/core';
import Backdrop from '@material-ui/core/Backdrop';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Fade from '@material-ui/core/Fade';
import Modal from '@material-ui/core/Modal';
import Paper from '@material-ui/core/Paper';
import Popover from '@material-ui/core/Popover';
import { withStyles } from '@material-ui/core/styles';
import LocationOnIcon from '@material-ui/icons/LocationOnRounded';
import GoogleMapReact from 'google-map-react';
import React, { useState } from 'react';

import MapView from '../containers/mapContainer';
import { Color } from '../styles/styles';
import ChartView from '../containers/chartContainer';

export interface Props {
  classes: any;
  title: string;
  key: string;
  lat: number;
  lng: number;
  getStationData: Function;
}

const styles: object = (theme: any) => ({
  stationContainer: {
    display: 'flex',
    flexDirection: 'column',
    padding: '1em 0 1em 0',
    width: 180,
    backgroundColor: Color.secondColor,
    boxShadow:
      '0px 2px 4px -1px rgba(0,0,0,0.2),' +
      '0px 4px 5px 0px rgba(0,0,0,0.14),' +
      '0px 1px 10px 0px rgba(0,0,0,0.12)',
  },
  stationTitle: {
    margin: 4,
    textAlign: 'center',
    color: Color.gray,
    fontSize: 13.5,
  },
  stationButton: {
    margin: 12,
    backgroundColor: Color.cyan,
    '&:hover': {
      backgroundColor: Color.cyan700,
    },
  },
  stationIcon: {
    position: 'relative',
    left: '-0.5em',
    top: '-1em',
    color: Color.cyan700,
  },
});

const StationView = ({ classes, title, getStationData }: Props): React.ReactElement => {
  const [anchorEl, setAnchorEl] = React.useState<SVGSVGElement | null>(null);
  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  const handleClick = (event: React.MouseEvent<SVGSVGElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClickData = () => {
    getStationData('no-os-aker-brygge-001');
    handleOpenChart();
  };
  const [openChart, setOpenChart] = React.useState(false);

  const handleOpenChart = () => {
    setOpenChart(true);
  };

  const handleCloseChart = () => {
    setOpenChart(false);
  };

  return (
    <>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'left',
        }}
      >
        <Paper className={classes.stationContainer}>
          <Typography className={classes.stationTitle}>{title.toUpperCase()}</Typography>
          <Button
            variant="contained"
            className={classes.stationButton}
            onClick={handleClickData}
          >
            Data
          </Button>
          <Button variant="contained" className={classes.stationButton}>
            Enhet
          </Button>
        </Paper>
      </Popover>
      <LocationOnIcon
        fontSize={'large'}
        className={classes.stationIcon}
        onClick={handleClick}
      />
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={openChart}
        onClose={handleCloseChart}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={openChart}>
          <ChartView />
        </Fade>
      </Modal>
    </>
  );
};

export default withStyles(styles)(StationView);
